@import '../../../variables.scss';

.contact {
	margin-top: 20px;

	h5 {
		margin-bottom: 1px;
		font-size: 13px;
		color: var(--project-color-light, $MAGENTA);
		transition: color $ANIMATION_TIME;
	}

	a {
		font-family: 'roc-grotesk-wide', sans-serif;
		font-size: 15px;
		font-weight: 600;
	}
}

[data-theme='dark'] .contact h5 {
	color: var(--project-color-dark, $D_MAGENTA);
}
